.header_button{
    background-color: #4094EC;
    border-radius: 31px;
    padding: 8px 16px 8px 16px;
}
.header_cont_1_a{
    text-align: center;
}
.header_cont_1{
    padding-top: 10rem;
}
.header_free_community{
    font-family: 'General Sans', sans-serif;
    width: 90%;
    font-size: 76px;
    font-weight: 800;
    line-height: 96px;
    letter-spacing: 0.01em;
    text-align: center;
    margin-left: 5%;
    margin-top: 1rem;
}
.header_free{
    background: #ee0979;  /* fallback for old browsers */
    height: 1.5em;
    background-image: linear-gradient(90deg, rgba(64,148,236,1) 0%, rgba(6,214,160,1) 100%);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
}
.header_para_about{
    font-family: 'General Sans', sans-serif;
    font-size: 20px;
    font-weight: 450;
    line-height: 29px;
    letter-spacing: 0em;
    text-align: center;
    width: 57%;
    margin-left: 20%;
    margin-top: 1rem;
}

.header_cont_2_b_youtube{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background-color: white;
    border: 1px solid #DEE4ED;
    padding: 1em 2em;
    font-family: 'General Sans', sans-serif;
    font-size: 18px;
    color: #181E25;
    gap: 1em;
}
.header_cont_2_b_discord {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background-color: white;
    border: 1px solid #DEE4ED;
    padding: 1em 2em;
    font-family: 'General Sans', sans-serif;
    font-size: 18px;
    color: #181E25;
    gap: 1em;
}
.header_cont_2_b{
    margin-top: 2rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 2rem;
}

@media screen and (max-width: 500px){
    .header_cont_1{
        text-align: center;
    }
    .header_cont_2_b {
        flex-direction: column;
    }
}

